import React from 'react';
import styled from 'styled-components';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

const SelectBtn = styled.select`
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  padding-left: 0.5rem;
  font-size: 0.875rem;

  background: #ffffff;
  border: 3px solid #000000;
  border-radius: 10px;
  width: 100%;
`;

function SelectBox({ state, locationHandler, timeHandler, nextStage }) {
  return (
    <Container>
      {state.curStage === 1 ? (
        <>
          <Row className="mb-2">
            <Col xs="4"> </Col>
            <Col>
              <SelectBtn onChange={locationHandler} value={state.location}>
                <option>  </option>
                <option value=" 4"> 4</option>
                <option value=""></option>
                <option value=""></option>
              </SelectBtn>
            </Col>
          </Row>
          <Row className="mb-2">
            <Col xs="4"> </Col>
            <Col>
              <SelectBtn onChange={timeHandler} value={state.time}>
                <option>  </option>
                <option value="5 ">5 </option>
                <option value="10 ">10 </option>
                <option value="15 ">15 </option>
              </SelectBtn>
            </Col>
          </Row>
          <div className="d-grid gap-1">
            <Button variant="secondary" onClick={nextStage}>
              
            </Button>
          </div>
        </>
      ) : (
        <Button variant="secondary" onClick={nextStage}>
           
        </Button>
      )}
    </Container>
  );
}

export default SelectBox;
